import React from 'react';
export default class Adicionar extends React.Component {

  constructor() {
    super();
    this.state = {
      texto: ""
    };
  }

  confirmar() {
    if (this.state.texto != "") {
      this.props.onAdiciona(this.state.texto);
      this.setState({
        texto: ""
      });
    }
    else
      alert("Texto no pode ser vazio!");
  }

  render() {
    return <span>
      <input value={this.state.texto}
        onChange={(evento) => this.setState({ texto: evento.target.value })} />
      <button onClick={() => this.confirmar()}>Adicionar</button>
    </span>
  }
}


