import React from 'react';
import Adicionar from './Adicionar';
export default class ListarPublicacao extends React.Component {

  render() {
    return <ol>
      {this.props.itens.map(
        (comentario) => <li>
          {comentario.texto} - <span style={{ color: "gray" }}>{comentario.data}</span>
          <button onClick={() => this.props.onRemoveComentario(this.props.idPublicacao, comentario.id)} >Excluir</button>
        </li>)}
      <Adicionar onAdiciona={(valor) => this.props.onAdicionaComentario(this.props.idPublicacao, valor)} />
    </ol>
  }
}







