import React from 'react';
import ListarComentarios from './ListarComentario';
export default class ListarPublicacao extends React.Component {

  render() {
    return <ul>
      {this.props.itens.map(
        (item) => <li>
          {item.texto} - <span style={{ color: "gray" }}>{item.data}</span><br />
          {item.podeComentar?
            <button onClick={() => this.props.onBuscaComentarios(item.id)}>comentrios ({item.quantidadeComentarios}) </button>:
            ""}
          <button onClick={() => this.props.onRemovePublicacao(item.id)}>Excluir</button>
          {this.props.exibirComentariosId == item.id ?
            <ListarComentarios
              idPublicacao={item.id}
              itens={this.props.comentarios}
              onRemoveComentario={(idPublicacao, idComentario) => this.props.onRemoveComentario(idPublicacao, idComentario)}
              onAdicionaComentario={(idPublicacao, texto) => this.props.onAdicionaComentario(idPublicacao, texto)}
            /> : ""}
        </li>
      )}
    </ul>;
  }
}







