import React from 'react';
import API from '../API';
import Adicionar from './Adicionar';
import ListarPublicacao from './ListarPublicacao';
export default class PublicacaoPagina extends React.Component {

  constructor() {
    super();
    this.state = {
      itens: [],
      exibirComentariosId: null
    };
  }

  buscaComentarios(id) {
    API.get("publicacoes/" + id + "/comentarios/").then(
      (retorno) => this.setState({
        exibirComentariosId: id,
        comentarios: retorno.data
      }, () => this.atualizaContagem(id, retorno.data.length))
    );
  }

  atualizaContagem(id, valor) {
    this.setState(
      (antigo) => {
        antigo.itens = antigo.itens.map(
          (publicacao) => {
            if (id == publicacao.id) {
              publicacao.quantidadeComentarios = valor;
            }
            return publicacao;
          });
        return antigo;
      }
    );
  }

  adicionarComentario(id, texto) {
    API.post("publicacoes/" + id + "/comentarios/",
      {
        texto: texto
      }
    ).then(
      () => { this.buscaComentarios(id); }
    );
  }

  removeComentario(id, idComentario) {
    API.delete("publicacoes/" + id + "/comentarios/" + idComentario).then(
      () => { this.buscaComentarios(id); }
    );
  }

  removePublicacao(id) {
    API.delete("publicacoes/" + id).then(
      () => this.carregaPublicacoes()
    );
  }

  adicionaPublicacao(texto,podeComentar) {
    API.post("publicacoes/",
      {
        texto: texto,
        podeComentar:podeComentar
      }
    ).then(
      () => this.carregaPublicacoes()
    );
  }
  render() {
    return <div>
      <ListarPublicacao
        areas={this.state.areas}
        itens={this.state.itens}
        exibirComentariosId={this.state.exibirComentariosId}
        comentarios={this.state.comentarios}
        onAddComentario={(id, texto) => this.addComentario(id, texto)}
        onBuscaComentarios={(id) => this.buscaComentarios(id)}
        onRemovePublicacao={(id) => this.removePublicacao(id)}
        onRemoveComentario={(idPublicacao, idComentario) =>
          this.removeComentario(idPublicacao, idComentario)}
        onAdicionaComentario={(idPublicacao, texto) =>
          this.adicionarComentario(idPublicacao, texto)}
      />
      <br />
      <label>Adicionar publicao:</label>
      <br /><br />
      <input
            id="podeComentar"
            type="checkbox"
            checked={this.state.podeComentar}
            onChange={(evento) =>this.setState({podeComentar:evento.target.checked})} />
            <label htmlFor="podeComentar" >Pode comentar</label>
      <br /><br />
      <Adicionar onAdiciona={(valor) => this.adicionaPublicacao(valor, this.state.podeComentar)} />
    </div>;
  }
  
  carregaPublicacoes() {
    API.get("publicacoes/").then(
      (retorno) => this.setState({
        exibirComentariosId: null,
        comentarios: null,
        itens: retorno.data
      })
    );
  }

  carregaAreas() {
    API.get("areas/").then(
      (retorno) => this.setState({
        areas: retorno.data
      })
    );
  }

  componentDidMount() {
    this.carregaPublicacoes();
    this.carregaAreas();

  }
}


